@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
/* The typing effect */
@-webkit-keyframes typing {
  from {
    width: 0; }
  to {
    width: 100%; } }
@keyframes typing {
  from {
    width: 0; }
  to {
    width: 100%; } }

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }
@keyframes blink-caret {
  from,
  to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }

body {
  background-color: #eaebec;
  font-family: "Source Sans Pro", sans-serif;
  font-style: normal;
  font-size: 20px;
  color: #222222; }

h1,
h2,
h3,
h4,
h5 {
  color: #fff;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  line-height: 1.3;
  text-transform: uppercase;
  text-align: center; }

.img-responsive {
  margin: 0 auto; }

p {
  color: #222222;
  line-height: 1.3;
  word-wrap: break-word; }

a {
  color: #5f0f40;
  line-height: 1.3;
  text-decoration: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }
  a:hover, a:active, a:focus {
    color: #5f0f40;
    text-decoration: underline; }

::-moz-selection {
  background: #000;
  color: #fff; }

::selection {
  background: #000;
  color: #fff; }

.intro {
  background-color: #310e68;
  background-image: -webkit-linear-gradient(134deg, #310e68 0%, #5f0f40 74%);
  background-image: linear-gradient(316deg, #310e68 0%, #5f0f40 74%); }
  .intro .flex-container {
    padding-top: 40px; }
  .intro h1 {
    text-align: center;
    font-weight: bold;
    font-size: 42px; }
    .intro h1 span {
      display: block; }
  .intro .title-container {
    text-align: center; }
    .intro .title-container h2 {
      font-weight: normal;
      font-size: 32px;
      display: inline-block;
      border-right: 4px solid #fff;
      letter-spacing: 4px;
      -webkit-animation: blink-caret 0.75s step-end infinite;
      animation: blink-caret 0.75s step-end infinite; }
  .intro .image-container {
    padding-top: 40px;
    max-width: 480px;
    margin: 0 auto; }
  .intro .go-down {
    padding-bottom: 40px; }
    .intro .go-down a {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      height: 50px;
      width: 50px;
      border: 2px solid #fff;
      color: #fff;
      margin: 0 auto;
      text-decoration: none;
      background-color: transparent; }
      .intro .go-down a:hover, .intro .go-down a:active, .intro .go-down a:focus {
        background-color: #fff; }
        .intro .go-down a:hover i, .intro .go-down a:active i, .intro .go-down a:focus i {
          color: #5f0f40; }
      .intro .go-down a i {
        font-size: 20px;
        -webkit-transform: translateY(-8px);
        transform: translateY(-8px);
        -webkit-transition: color 0.3s ease;
        transition: color 0.3s ease;
        -webkit-animation: moveDown 1.8s 0.3s infinite ease-in-out;
        animation: moveDown 1.8s 0.3s infinite ease-in-out; }

@media (min-width: 768px) {
  .intro .flex-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-height: calc(100vh - 90px); }
  .intro h1 {
    text-align: left;
    font-size: 50px;
    margin-top: 0px; }
  .intro .title-container {
    text-align: left; }
    .intro .title-container h2 {
      font-size: 38px;
      margin-bottom: 0px; }
  .intro .image-container {
    padding-top: 0px;
    max-width: none; } }

@-webkit-keyframes moveDown {
  0% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px); }
  50% {
    -webkit-transform: translateY(8px);
    transform: translateY(8px); }
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px); } }

@keyframes moveDown {
  0% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px); }
  50% {
    -webkit-transform: translateY(8px);
    transform: translateY(8px); }
  100% {
    -webkit-transform: translateY(-8px);
    transform: translateY(-8px); } }

.portfolio {
  padding: 40px 0px; }
  .portfolio h3 {
    color: #310e68;
    text-align: center;
    font-size: 34px; }
  .portfolio .portfolio-container {
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    justify-content: center;
    margin-top: 30px; }

@media (min-width: 768px) {
  .portfolio h3 {
    font-size: 38px; }
  .portfolio .portfolio-container {
    flex-direction: row;
    flex-wrap: wrap; } }

.project-container {
  max-width: 480px;
  margin: 15px auto; }
  .project-container .card {
    border-radius: 0px;
    box-shadow: 0px 13px 21px -5px rgba(0, 0, 0, 0.4);
    width: 100%;
    -webkit-transition: -webkit-box-shadow 0.3s ease;
    -webkit-transition: box-shadow 0.3s ease;
    transition: box-shadow 0.3s ease; }
    .project-container .card:hover, .project-container .card:active, .project-container .card:focus {
      box-shadow: none; }
    .project-container .card .image-container {
      background-color: #fff;
      padding: 15px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
    .project-container .card .info-container {
      text-align: center;
      padding: 15px;
      background-color: #310e68; }
      .project-container .card .info-container p {
        margin: 0px;
        color: #fff; }
        .project-container .card .info-container p.name {
          font-weight: bold; }
          .project-container .card .info-container p.name span {
            display: block;
            padding-top: 5px; }
            .project-container .card .info-container p.name span.type {
              font-weight: normal; }
        .project-container .card .info-container p.description {
          font-weight: 300; }
      .project-container .card .info-container .link-container {
        text-align: center;
        padding: 20px 0px 15px; }
        .project-container .card .info-container .link-container a {
          font-size: 15px;
          color: #fff;
          text-decoration: underline;
          opacity: 1; }
          .project-container .card .info-container .link-container a:hover, .project-container .card .info-container .link-container a:active, .project-container .card .info-container .link-container a:focus {
            opacity: 0.6; }

@media (min-width: 768px) {
  .project-container .card .info-container .link-container a {
    font-size: 20px; } }

@media (min-width: 992px) {
  .project-container {
    width: calc(50% - 30px); } }

@media (min-width: 1200px) {
  .project-container {
    width: calc(33.33% - 30px);
    margin: 15px;
    max-width: none; } }

.stack-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 15px 0px 0px;
  border-top: 1px solid #fff; }
  .stack-container .stack {
    width: 20%;
    padding: 8px; }

